import React, {PropsWithChildren} from 'react';
import {Table, TableHeader, TableBody, TableRow, TableHeaderCell, TableBodyCell, TableContainer, TableWrapper} from "./styled";

type TableProps = PropsWithChildren<{}>;

type SubComponents = {
  Header: typeof TableHeader;
  Body: typeof TableBody;
  Row: typeof TableRow;
  HeaderCell: typeof TableHeaderCell;
  BodyCell: typeof TableBodyCell;
  Container: typeof TableContainer;
  Wrapper: typeof TableWrapper;
};

const TableComponent: React.FC<TableProps> & SubComponents = ({children}: TableProps) => {
  return (
    <Table>
      {children}
    </Table>
  );
};

TableComponent.Header = TableHeader;
TableComponent.Body = TableBody;
TableComponent.Row = TableRow;
TableComponent.HeaderCell = TableHeaderCell;
TableComponent.BodyCell = TableBodyCell;
TableComponent.Container = TableContainer;
TableComponent.Wrapper = TableWrapper;

export default TableComponent;
