import * as React from 'react';
import Layout from '../components/common/Layout/Layout';
import ArticleCardItem, {
  CardSize,
} from '../components/common/ArticleCardItem/ArticleCardItem';
import { NewsPageHeader } from '../components/news';
import { graphql, PageProps } from 'gatsby';
import { mapArticleNodeToArticlePreviewInfo } from '../mappers';
import {
  DesktopCardsContainer,
  MediumCards,
  MobileCardsContainer,
  NewsContentContainer,
  NewsList,
  SmallCards,
  SmallCardWrapper,
} from '../components/news/styled';
import { useLunr } from 'react-lunr';
import { ArticlePreviewInfo } from '../types';
import {
  GatsbySeoComponent,
  MetaData,
  TopLevelPageJSONLDSchemas,
} from '../components/common/SEO';
import { NoResultsText } from '../components/common/styled';
import { useInView } from 'react-cool-inview';
import useWindowSize, { isDesktop } from '../hooks/useWindowSize';

const NewsPage = ({ data }: PageProps<Queries.NewsPageQuery>) => {
  const [searchString, setSearchString] = React.useState<string>('');
  const [articlesForRenderAmount, setArticlesForRenderAmount] = React.useState(10);

  const rawNews: ArticlePreviewInfo[] = React.useMemo(() => {
    return data.allMdx.edges.map((edge) => mapArticleNodeToArticlePreviewInfo(edge.node));
  }, []);

  const filteredNews: ArticlePreviewInfo[] = useLunr(
    searchString
      .trim()
      .split(' ')
      .map((word) => `${word} ${word}*`)
      .join(' '),
    data.localSearchNews?.index,
    data.localSearchNews?.store,
  ) as ArticlePreviewInfo[];

  const news = searchString ? filteredNews : rawNews;

  const separatedNewsByCardSize = {
    largeCard: news.slice(0, 1),
    mediumCards: news.slice(1, 5),
    smallCards: news.slice(5, articlesForRenderAmount),
  };

  const { observe } = useInView({
    // For better UX, we can grow the root margin so the data will be loaded earlier
    rootMargin: '50px 0px',
    // When the last item comes to the viewport
    onEnter: () => {
      setArticlesForRenderAmount((st) => st + 5);
    },
  });

  const windowSize = useWindowSize();
  const isDesktopScreen = isDesktop(windowSize.width as number);

  return (
    <>
      <MetaData
        title="The Latest News in A2P SMS and 10DLC | Telgorithm"
        description="Stay up to date on the latest news in A2P SMS and 10DLC and learn what it takes to provide the next level of business messaging experiences for your customers."
      />
      <TopLevelPageJSONLDSchemas url="https://www.telgorithm.com/news/" />
      <GatsbySeoComponent canonicalUrl="https://www.telgorithm.com/news/" />
      <Layout>
        <NewsContentContainer>
          <NewsPageHeader searchString={searchString} setSearchString={setSearchString} />
          {news.length ? (
            <NewsList>
              <DesktopCardsContainer>
                {separatedNewsByCardSize.largeCard.map((articleItem) => (
                  <ArticleCardItem
                    article={articleItem}
                    key={articleItem.articleId}
                    cardSize={CardSize.L}
                  />
                ))}
                {separatedNewsByCardSize.mediumCards.length > 0 && (
                  <MediumCards>
                    {separatedNewsByCardSize.mediumCards.map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                )}
                {isDesktopScreen && separatedNewsByCardSize.smallCards.length > 0 && (
                  <SmallCards>
                    {separatedNewsByCardSize.smallCards.map((articleItem, index, arr) => (
                      <SmallCardWrapper
                        ref={
                          (index === arr.length - 1 ? observe : null) as (
                            element?: any,
                          ) => void
                        }
                      >
                        <ArticleCardItem
                          article={articleItem}
                          key={articleItem.articleId}
                          cardSize={CardSize.S}
                        />
                      </SmallCardWrapper>
                    ))}
                  </SmallCards>
                )}
              </DesktopCardsContainer>
              <MobileCardsContainer>
                <div>
                  <MediumCards>
                    {[
                      ...separatedNewsByCardSize.largeCard,
                      ...separatedNewsByCardSize.mediumCards,
                    ].map((articleItem) => (
                      <ArticleCardItem
                        article={articleItem}
                        key={articleItem.articleId}
                        cardSize={CardSize.M}
                      />
                    ))}
                  </MediumCards>
                </div>
                <div>
                  {!isDesktopScreen && separatedNewsByCardSize.smallCards.length > 0 && (
                    <SmallCards>
                      {separatedNewsByCardSize.smallCards.map(
                        (articleItem, index, arr) => (
                          <SmallCardWrapper
                            ref={
                              (index === arr.length - 1 ? observe : null) as (
                                element?: any,
                              ) => void
                            }
                          >
                            <ArticleCardItem
                              article={articleItem}
                              key={articleItem.articleId}
                              cardSize={CardSize.S}
                            />
                          </SmallCardWrapper>
                        ),
                      )}
                    </SmallCards>
                  )}
                </div>
              </MobileCardsContainer>
            </NewsList>
          ) : (
            <NoResultsText>No results for "{searchString}"</NoResultsText>
          )}
        </NewsContentContainer>
      </Layout>
    </>
  );
};

export default NewsPage;

export const query = graphql`
  query NewsPage {
    localSearchNews {
      index
      store
    }
    allMdx(
      filter: { fileAbsolutePath: { regex: "//news//" } }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            articleType
            date(formatString: "MMMM DD, YYYY")
            title
            previewImage {
              childrenImageSharp {
                gatsbyImageData(
                  layout: FIXED
                  formats: WEBP
                  webpOptions: { quality: 50 }
                  quality: 50
                )
              }
            }
          }
          id
          slug
        }
      }
    }
  }
`;
