import styled from "styled-components";
import { device } from "../../../utils/layoutBreakpoints";
import { colors, opacityToHex } from "../../../utils/colors";

export const TableHeaderCell = styled.th`
  font-size: 16px;
  font-family: "Rubik";
  font-weight: 600;
  line-height: 22px;
  text-align: left;

  @media ${device.laptop} {
    font-size: 18px;
    line-height: 24px;
  }
`;

export const TableBodyCell = styled.td`
  font-size: 16px;
  font-family: "Rubik";
  font-weight: 400;
  line-height: 22px;
  vertical-align: middle;

  @media ${device.laptop} {
    font-size: 18px;
    line-height: 24px;
  }
`;

export const TableRow = styled.tr`
  ${TableBodyCell} {
    padding: 9px 9px 9px 0px;

    &:first-child {
      padding-left: 24px;
      border-top-left-radius: 8px;
      border-bottom-left-radius: 8px;
    }

    &:last-child {
      padding-right: 24px;
      border-top-right-radius: 8px;
      border-bottom-right-radius: 8px;
    }
  }

  ${TableHeaderCell} {
    padding: 9px 9px 9px 0px;

    &:first-child {
      padding-left: 24px;
    }
  }
`;

export const TableHeader = styled.thead``;

export const TableBody = styled.tbody`
  ${TableRow}:nth-child(2n+1) {
    background-color: ${colors.primary.blue + opacityToHex["0.1"]};
  }
`;

export const Table = styled.table`
  table-layout: fixed;

  @media ${device.laptop} {
    margin: 0;
    width: 100%;
  }
`;

export const TableContainer = styled.div`
  overflow-x: hidden;
  position: relative;
  @media ${device.tablet} {
    width: auto;
  }
  @supports (-webkit-touch-callout: none) {
    &::after {
      position: absolute;
      content: "";
      display: block;
      bottom: 2px;
      left: 0;
      width: 100%;
      height: 5px;
      background-color: ${colors.primary.gray500};
      border-radius: 5px;
      z-index: -1;
      @media ${device.tablet} {
        display: none;
      }
    }
  }
`;

export const TableWrapper = styled.div`
  overflow-x: scroll;
  scrollbar-color: ${colors.secondary.gray700};
  padding-bottom: 10px;

  /* Chrome, Edge, and Safari */
  &::-webkit-scrollbar {
    width: 12px;
    height: 10px !important;
  }

  &::-webkit-scrollbar-track {
    background: ${colors.secondary.gray300};
    border-radius: 5px;
    height: 5px !important;
  }

  &::-webkit-scrollbar-thumb {
    background-color: ${colors.primary.gray500};
    border-radius: 10px;
    height: 5px !important;
  }
  &:hover::-webkit-scrollbar-thumb {
    background-color: ${colors.primary.gray500};
  }

  @media ${device.tablet} {
    overflow-x: auto;
    table {
      min-width: auto;
    }
  }
  table {
    min-width: 850px;
    width: 100%;
    table-layout: auto;
  }
`;
